@import 'shared/StyleValues.scss';
@import 'shared/Animations.scss';

.RightImageForm {
  position: fixed;
  margin-top: $stepSize * 2;
  height: calc(100% - #{$stepSize * 4});
  width: 100%;

  .ContextBox {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
  }

  $leftPadding: 15%;

  .LoginBox {
    position: relative;
    width: 100%;

    .Title {
      padding-left: $leftPadding;
      padding-right: 45%;
      text-align: left;
      font-size: $midArticle;
    }

    .SubTitle {
      padding-left: $leftPadding;
      padding-right: 40%;
      text-align: left;
      font-size: $topArticle;
    }
  }

  .InputFiled {
    margin-top: 2%;

    .Decoration {
      position: relative;
      width: 100%;
      top: 30%;

      .InputLine {
        position: relative;
        width: 100%;
        height: 65px;
        border-top: 1px solid black;

        .Input {
          height: 100%;
          padding-top: 20px;
          padding-right: 45%;
          margin-left: $leftPadding;
        }

        .Arrow {
          @include square_px(65px);
          padding: 7.5px 7.5px 7.5px 7.5px;
          position: relative;
          pointer-events: all;
          left: calc(65% - 48px);
          top: -65px;
        }
      }
    }
  }

  .ImageBox {
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 2.5%;
    pointer-events: none;
    height: 100%;
    width: 40%;

    .Image {
      position: relative;
      display: block;
      margin: 0 auto;
      height: 95%;
    }

    .InnerText {
      position: absolute;
      $widthSize: 62.5%;
      width: $widthSize;
      top: 60%;
      left: calc(50% - #{$widthSize / 2});

      span {
        letter-spacing: -1px;
        text-align: left;
        font-size: $topArticle;
      }
    }

    .Arrow {
      position: absolute;
      pointer-events: all;
      right: 5px;
      @include square(3);
    }
  }
}