@import 'shared/StyleValues.scss';
@import 'shared/Animations.scss';

.Documents {
    position: fixed;
    pointer-events: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .Cover {
        position: fixed;
        pointer-events: all;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba($color: #FFF, $alpha: 0.75);
    }

    .Context {
        position: absolute;
        top: $stepSize * 3;
        bottom: $stepSize * 3;
        left: 0;
        right: 0;

        .TopBar {
            margin-left: $sidemargin *2;

            .GoBack {
                @include square(3);
            }
        }

        .TitleBar {
            margin-top: 15px;
            margin-left: $sidemargin *2;

            .Title {
                display: inline-block;
                font-size: 54px;
                line-height: calc(100% - 4px);
                @include underline(3px);
            }
        }

        .PathBar {
            margin-top: 10px;
            margin-left: $sidemargin *2;

            .Path {
                display: inline-block;
                font-size: 32px;
            }
        }

        .IconContainer {
            position: absolute;
            bottom: 0;
            right: $sidemargin;
        }
    }
}