@import 'shared/StyleValues.scss';
@import 'shared/Animations.scss';

.Homepage {
    position: fixed;
    pointer-events: none;
    top: 0;
    background-size: cover;
    height: 100%;
    width: 100%;

    .Context {
        margin: 5% $sidemargin 5% $sidemargin;
        height: 90%;

        .MessageBox {
            position: relative;
            top: 35%;
            left: $stepSize *2;

            div {
                height: $smallArticle;
                font-size: $title;
            }
        }

        .ConceptsImage {
            position: fixed;
            margin-top: 5%;
            right: $sidemargin;
            height: 70%;
        }

        .ArrowLink {
            position: relative;
            top: 5%;
            left: 32.5%;


            .Arrow {
                position: relative;
                @include square(5);
                pointer-events: all;
            }
        }
    }

}